.skillsBlock {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: darkcyan;
}

.skillsContainer {
    display: flex;
    border: 1px solid black;
    flex-direction: column;
    align-items: center;
}

.skills {
    width: 100%;
    border: 1px solid black;
    background-color: lightgray;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
}

.skill {
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    width: 250px;
    justify-content: center;
    align-items: center;
}

.skill img {
    width: 100%;
}

.title {
    font-size: 30px;
}

.description {
    text-align: center;
}